.pez-frame {
  margin: 0;
  padding: 0;
  display: block;
}

.pez {
  padding: 0;
  border: 1px solid #333;
  border-radius: 90px;
  -webkit-transition: all 0.15s;
  -o-transition: all 0.15s;
  transition: all 0.15s;
}

.pez-frame:hover > .pez {
  border: 1px solid rgba(255, 255, 255, 0.4);
}

.pez.white {
  -webkit-box-shadow: 0px 0px 32px rgba(255, 255, 255, 1);
          box-shadow: 0px 0px 32px rgba(255, 255, 255, 1);
  background-color: rgba(255, 255, 255, 0.9);
  border-color: rgba(255, 255, 255, 0.8);
  -webkit-animation: white-glow 0.25s ease-in forwards;
          animation: white-glow 0.25s ease-in forwards;
}

.pez-frame:hover > .pez.white {
  border-color: rgba(255, 255, 255, 0.4);
}

.pez.yellow {
  -webkit-box-shadow: 0px 0px 32px rgba(218, 187, 34, 1);
